import { createReducer } from "@reduxjs/toolkit"
import pJSON from '../../../package.json'

import {
  error,
  modalOpen,
  modalFullscreen,
} from "./actions"

export const appSlice = {
  pJSON,
  error: null,
  showCTA: true,
  modalOpen: false,
  modalFullscreen: false,
}

const appReducer = createReducer(appSlice, {

  [error]: (state, action) => {
    state.error = action.error
    return state
  },

  [modalOpen]: (state, action) => {
    state.modalOpen = action.modalOpen
    return state
  },

  [modalFullscreen]: (state, action) => {
    state.modalFullscreen = action.modalFullscreen
    return state
  },
  
})

export { appReducer }
