import { createReducer } from '@reduxjs/toolkit'
import { screens } from './data/screens'

import {
  error,
  currentScreenId,
} from "./actions"

export const jsxformSlice = {
  error: null,
  screens,
  currentScreenId: `X001`,
}

const jsxformReducer = createReducer(jsxformSlice, {

  [error]: (state, action) => {
    state.error = action.error
    return state
  },

  [currentScreenId]: (state, action) => {
    state.currentScreenId = action.currentScreenId
    return state
  },
  
})

export { jsxformReducer }
