import React from 'react'
// import moment from 'moment'
import clsx from 'clsx'
import {
    makeStyles,
    useTheme,
    IconButton,
    Dialog,
    DialogContent,
    DialogContentText,
    DialogActions,
    Slide,
    CardHeader,
} from '@material-ui/core/'
import { useSelector } from 'react-redux'
import { toggleDialog } from '../redux/app/actions'
import { Icon } from '../theme'
// import { Screen } from './'

const Transition = React.forwardRef(function Transition(props, ref) {
  return 	<Slide 
	  			direction={`down`} 
	  			ref={ref} 
	  			{...props} 
  			/>
})

const useStyles = makeStyles(theme => ({
	modal: {
		zIndex: 199999,
	},
	cardHeader:{
		minWidth: 360,
	},
	clearBG:{
		// background: 'none',
	},	
	btnTxt:{
		marginLeft: theme.spacing(),
		marginRight: theme.spacing(),
	},
	white: {
		color: 'white',
	},
	titleTxt: {
		// paddingTop: theme.spacing(0.5),
		marginRight: theme.spacing(2),
	},
	dialogTitle:{
		display: 'flex',
	},
	grow:{
		flexGrow: 1,
	},	
}))

export default function Modal() {

	const classes = useStyles()
	const appSlice = useSelector(state => state.app)
	const {
		modalOpen,
		modalFullscreen,
	} = appSlice
	
	const theme = useTheme()
	const primary = theme.palette.primary.main

	if (!modalOpen) return null

	return	<Dialog 
				open
				keepMounted
				fullWidth
				maxWidth={`sm`}
				fullScreen={ modalFullscreen }
				TransitionComponent={ Transition }
				className={ clsx( classes.modal ) }
				onClose={(e) => {
					e.preventDefault() 
					toggleDialog(false)} 
				}>
				<CardHeader 
					title={ `Always Free. Always Open Source` }
					subheader={ `${process.env.REACT_APP_APP}` }
					
					className={ clsx( classes.cardHeader ) }
					avatar={ <div>
								<Icon icon={`blokey`} color={primary} /> 
							</div>
					}
					action={<React.Fragment>
						
						<IconButton
							className={ clsx(classes.clearBG) }
							color={`primary`}
							onClick={(e) => { 
							e.preventDefault()
							toggleDialog(false)
						}}>
							<Icon icon={`close`} color={`inherit`} />
						</IconButton>

					</React.Fragment>}
				/>

				<DialogContent>	
					<DialogContentText>
			            The WordPress theme seen here is Open Source and 
			            can be downloaded free from GitHub. 
			            Without needing an email.
			        </DialogContentText>
					
				</DialogContent>

				

				

				<DialogActions>
					<div className={ clsx(classes.grow) } />
					

					<IconButton 
			          	variant={`contained`}
			          	color={`primary`}
			          	onClick={(e) => {
			          		e.preventDefault() 
			          		window.open(`https://github.com/listingslab-software/wordpress-theme-pwa`, `_blank`) 
			          		toggleDialog(false)
			          	}}>
			          	<Icon icon={`github`} color={primary} />  
			          </IconButton>

		          	<div className={ clsx(classes.grow) } />
		        </DialogActions>
			</Dialog>
}



