import { createReducer } from "@reduxjs/toolkit"

import {
  error,
} from "./actions"

export const wpjsonSlice = {
  error: null,
}

const wpjsonReducer = createReducer(wpjsonSlice, {

  [error]: (state, action) => {
    state.error = action.error
    return state
  },
  
})

export { wpjsonReducer }
