import React from 'react'
import clsx from 'clsx'
import {
    makeStyles,
    List,
    ListItem,
    ListItemText,
} from '@material-ui/core/'
import { useSelector } from 'react-redux'
import { toggleDialog } from '../redux/app/actions'
import { getScreenfromId } from '../redux/jsxform/actions'
// import { Icon } from '../theme'

const useStyles = makeStyles(theme => ({
	selectScreen: {	
	},
}))

export default function Screen(props) {

	const classes = useStyles()
	const jsxformSlice = useSelector(state => state.jsxform)
	const {
		currentScreenId,
		screens,
	} = jsxformSlice
	const screen = getScreenfromId(currentScreenId)
	console.log ('screen', screen)
	

	return	<List dense className={clsx( classes.selectScreen )}>
	        	<ListItem 
	        		button
	        		onClick={(e) => {
          				e.preventDefault()
          				toggleDialog(false)
          			}}>
	        		<ListItemText 
	        			primary="Work"
	        		/>
	        	</ListItem>
	        	<ListItem 
	        		button
	        		onClick={(e) => {
          				e.preventDefault()
          				toggleDialog(false)
          			}}>
          			<ListItemText 
	        			primary="Life"
	        		/>
	        	</ListItem>
	        	<ListItem 
	        		button
	        		onClick={(e) => {
          				e.preventDefault()
          				toggleDialog(false)
          			}}>
          			<ListItemText 
	        			primary="Balance"
	        		/>
	        	</ListItem>
	        </List>
}



