import { createAction } from '@reduxjs/toolkit'
import { 
	getStore, 
	// getHistory,
} from '../../App'

export const error = createAction(`APP/ERROR`)
export const modalOpen = createAction(`APP/MODAL/TOGGLE/OPEN`)
export const modalFullscreen = createAction(`APP/MODAL/TOGGLE/FULLSCREEN`)

export const toggleDialog = bool => {
	const store = getStore()
	store.dispatch({type: `APP/MODAL/TOGGLE/OPEN`, modalOpen: bool })
	return true
}

export const toggleFullscreen = (bool) => {
	const store = getStore()
	store.dispatch({type: `APP/MODAL/TOGGLE/FULLSCREEN`, modalFullscreen: bool })
	return true
}










