import React from 'react'
import { useSelector } from 'react-redux'
import { toggleDialog } from '../redux/app/actions'
import clsx from 'clsx'
import {
    makeStyles,
    useTheme,
    ButtonBase,
    Typography,
} from '@material-ui/core/'
import { Icon } from '../theme'

const useStyles = makeStyles(theme => ({
	appBtn:{
		height: 70,
		maxWidth: 325,
		width: '100%',
		padding: theme.spacing(),
		borderRadius: theme.spacing(0.5),
	},
	coloredText: {
		color: 'rgba(255,255,255, 1)',
		fontSize: 18,
		textTransform: 'uppercase',
		paddingTop: theme.spacing(1.2),
	},
	icon:{
		position: 'absolute',
		right: theme.spacing(3),
		top: theme.spacing(2.5)
	},
	labelText: {
		position: 'absolute',
		left: theme.spacing(2),
		top: theme.spacing(1.25)
	},
}))

export default function CallToAction() {

	const classes = useStyles()
	const theme = useTheme()
	const primary = theme.palette.primary.main
	const secondary = theme.palette.secondary.main
	const appSlice = useSelector(state => state.app)
	const {
		showCTA,
	} = appSlice

	if (!showCTA) return null

	return	<ButtonBase 
				className={clsx(classes.appBtn)}
				style={{ background: 'linear-gradient(' + secondary + ' 0%, ' + primary + ' 100%)' }}
				onClick={(e) => {
				e.preventDefault()
				toggleDialog(true)
			}}>

				<div className={classes.icon}>
					<Icon icon={`blokey`} color={`white`} />
				</div>
				
				<div className={classes.labelText}>
					<Typography 
						variant={`h6`}
						className={clsx(classes.coloredText)}>
						{ `${process.env.REACT_APP_APP}` }
					</Typography>
				</div>

			</ButtonBase>
}